.coinElem {
    display: flex;
    justify-content: center;
}

.coinBlock {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;

    min-width: 300px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.symbol {
    text-transform: uppercase;
}

.coinData {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
}

.price {
    width: 110px;
}

.volume {
    width: 155px;
}

.marketcap {
    width: 230px;
}

.redPercent {
    width: 100px;
    color: #f00606;
}


.greenPercent {
    width: 100px;
    color: #11d811;
}